/* eslint-disable no-unused-vars */
/* eslint-disable no-undef */

import {
  doc,
  collection,
  getDocs,
  addDoc,
  updateDoc,
  deleteDoc,
  query,
  where,
  orderBy,
  getCountFromServer,
  limit,
  getAggregateFromServer,
  sum,
} from "firebase/firestore";
import FirebaseService from "./FirebaseService";

class EntradasService {
  constructor() {
    this.entradasCollection = collection(FirebaseService.db, "entradas");
    this.porPagina = 1;
    this.pagina = 2;
  }

  addEntradas = async (entradas) => {
    //await setDoc(doc(this.db, "entradas", "two"), entradas);
    await addDoc(this.entradasCollection, entradas);
  };

  getEntradas = async () => {
    const entradasSnap = await getDocs(this.entradasCollection);
    const entradasList = await entradasSnap.docs.map((doc) => doc.data());
    return entradasList;
  };

  addEntradasC = (entradas) => {
    //await setDoc(doc(this.db, "entradas", "two"), entradas);
    return addDoc(this.entradasCollection, entradas);
  };

  updEntradasC = (entradas) => {
    const eRef = doc(FirebaseService.db, "entradas", entradas.id);
    return updateDoc(eRef, entradas);
  };

  getEntradasCPaginada = () => {
    return getDocs(
      query(
        this.entradasCollection,
        orderBy("ano", "desc"),

        limit(this.porPagina)
      )
    );
  };

  getEntradasC = () => {
    //return getDocs(this.entradasCollection);
    return getDocs(
      query(this.entradasCollection, orderBy("ano", "desc"), orderBy("mes"))
    );
  };

  getQtdEntradas = async () => {
    //return this.entradasCollection.count();
    const q = this.entradasCollection;
    const snapshot = await getCountFromServer(q);
    const count = snapshot.data().count;
    return count;
  };

  getSumEntradasMesAno = (mes, ano) => {
    const q = query(
      this.entradasCollection,
      where("mes", "==", mes),
      where("ano", "==", ano)
    );
    return getAggregateFromServer(q, {
      sumValor: sum("valor"),
    });
  };

  removeEntradasC = (id) => {
    return deleteDoc(doc(FirebaseService.db, "entradas", id));
  };

  getRecebimentosMesAno = (mes, ano) => {
    const q = query(
      this.entradasCollection,
      where("mes", "==", mes),
      where("ano", "==", ano)
    );
    //const q = query(this.entradasCollection, where("tipo", "=", "E"));
    //const q = query(this.entradasCollection, where("idade", "in", [18]));
    return getDocs(q);
  };

  getRecebimentosMesAnoAsync = async (mes, ano) => {
    let res = await this.getRecebimentosMesAno(mes, ano);
    let _entradas = res.docs.map((doc) => {
      return { id: doc.id, dados: doc.data() };
    });
    let _valorTotalEntradas = _entradas.reduce((total, e) => {
      return total + Number.parseFloat(e.dados.valor);
    }, 0.0);
    return {
      entradas: _entradas,
      valorTotalEntradas: _valorTotalEntradas,
    };
  };
}

export default new EntradasService();
