import { getAuth, signOut, signInWithEmailAndPassword } from "firebase/auth";
class LoginService {
  static auth = getAuth();
  static isUserLoggedIn = () => {
    return LoginService.auth.currentUser != null;
  };
  static enter = (login, senha) => {
    return signInWithEmailAndPassword(
      LoginService.auth,
      login,
      senha
    );
  };
  static exit = () => {
    return signOut(LoginService.auth);
  };
  static getUser(){
    return LoginService.auth.currentUser;
  }
}
export default LoginService;
