/* eslint-disable no-unused-vars */
/* eslint-disable no-undef */

import {
  doc,
  collection,
  getDocs,
  addDoc,
  updateDoc,
  deleteDoc,
  query,
  where,
  orderBy,
  getCountFromServer,
  limit,
  getAggregateFromServer,
  sum,
} from "firebase/firestore";
import FirebaseService from "./FirebaseService";

class SaidasService {
  constructor() {
    this.saidasCollection = collection(FirebaseService.db, "saidas");
    this.porPagina = 1;
    this.pagina = 2;
  }

  addSaidas = async (saidas) => {
    //await setDoc(doc(this.db, "saidas", "two"), saidas);
    await addDoc(this.saidasCollection, saidas);
  };

  getSaidas = async () => {
    const saidasSnap = await getDocs(this.saidasCollection);
    const saidasList = await saidasSnap.docs.map((doc) => doc.data());
    return saidasList;
  };

  addSaidasC = (saidas) => {
    //await setDoc(doc(this.db, "saidas", "two"), saidas);
    return addDoc(this.saidasCollection, saidas);
  };

  updSaidasC = (saidas) => {
    const eRef = doc(FirebaseService.db, "saidas", saidas.id);
    return updateDoc(eRef, saidas);
  };

  getSaidasCPaginada = () => {
    return getDocs(
      query(
        this.saidasCollection,
        orderBy("ano", "desc"),

        limit(this.porPagina)
      )
    );
  };

  getSaidasC = () => {
    //return getDocs(this.saidasCollection);
    return getDocs(
      query(this.saidasCollection, orderBy("ano", "desc"), orderBy("mes"))
    );
  };

  getQtdSaidas = async () => {
    //return this.saidasCollection.count();
    const q = this.saidasCollection;
    const snapshot = await getCountFromServer(q);
    const count = snapshot.data().count;
    return count;
  };

  getSumSaidasMesAno = (mes, ano) => {
    const q = query(
      this.saidasCollection,
      where("mes", "==", mes),
      where("ano", "==", ano)
    );
    return getAggregateFromServer(q, {
      sumValor: sum("valor"),
    });
  };

  removeSaidasC = (id) => {
    return deleteDoc(doc(FirebaseService.db, "saidas", id));
  };

  getRecebimentosMesAno = (mes, ano) => {
    const q = query(
      this.saidasCollection,
      where("mes", "==", mes),
      where("ano", "==", ano)
    );
    //const q = query(this.saidasCollection, where("tipo", "=", "E"));
    //const q = query(this.saidasCollection, where("idade", "in", [18]));
    return getDocs(q);
  };

  getRecebimentosMesAnoAsync = async (mes, ano) => {
    let res = await this.getRecebimentosMesAno(mes, ano);
    let _saidas = res.docs.map((doc) => {
      return { id: doc.id, dados: doc.data() };
    });
    let _valorTotalSaidas = _saidas.reduce((total, e) => {
      return total + Number.parseFloat(e.dados.valor);
    }, 0.0);
    return {
      saidas: _saidas,
      valorTotalSaidas: _valorTotalSaidas,
    };
  };
}

export default new SaidasService();
