<template>
  <q-page class="q-pa-lg bg-light-blue window-height window-width justify-center ">
    <div class="q-pa-md">
      
      <q-card square bordered class="q-pa-sm shadow-5 fixed-center">

        <q-card-section class="text-center">
          <div class="text-grey-9 text-h5 text-weight-bold">Autenticação</div>
          <div class="text-grey-8">Digite suas credenciais para acesso</div>
        </q-card-section>

        <q-separator inset />

        <q-card-section>
          <q-form class="q-gutter-md">
            <q-input square filled clearable v-model="email" type="email" label="E-mail" />
            <q-input square filled clearable v-model="senha" type="password" label="Senha" />
          </q-form>
        </q-card-section>

        <q-separator class="bg-blue" inset />

        <q-card-actions class="q-px-md">
          <q-btn unelevated color="primary" size="lg" class="full-width" label="Login" @click="entrar" />
        </q-card-actions>
        <q-card-section class="text-center q-pa-none"></q-card-section>
      </q-card>
    </div>
  </q-page>
