/* eslint-disable no-undef */
import { createRouter, createWebHashHistory } from "vue-router";
import HomeView from "../views/HomeView.vue";
import LoginView from "@/views/auth/LoginView.vue";
import MesAtualView from "@/views/registros/MesAtualView.vue";

import LoginService from "@/services/LoginService";

const routes = [
  {
    path: "/",
    name: "home",
    component: HomeView,
  },
  {
    path: "/auth/login",
    name: "login",
    component: LoginView,
  },
  {
    path: "/registros/mes-atual",
    name: "mesatual",
    component: MesAtualView,
  },
  {
    path: "/about",
    name: "about",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/AboutView.vue"),
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

router.beforeEach((to, from) => {
  //console.log(`de ${from.name} para ${to.name}`);

  console.log("userlog ", LoginService.isUserLoggedIn());

  if (LoginService.isUserLoggedIn() == false && to.name != "login") {
    return { name: "login" };
  }

  /*if (!AuthService.validaPermissao(to.path)) {
    router.push('/nao-autorizado')
    return false;
  }*/

  return true;
});

export default router;
