  <q-card flat bordered style="min-width: 90vw">

   <q-card-section class="text-center">
    <div class="text-primary text-h5 text-weight-bold">Mês atual {{ mesAtual.getMonth() + 1 }}/{{
     mesAtual.getFullYear()
    }}</div>
    <div class="text-grey-8">Dados do mês atual</div>
   </q-card-section>

   <q-tabs v-model="tab" dense class="" active-color="secondary" indicator-color="grey-7" align="justify">
    <q-tab name="entradas" label="entradas" icon="attach_money">
     <!--<q-badge color="red" floating>2</q-badge>-->
    </q-tab>
    <q-tab name="saidas" label="Saídas" icon="payments" />
    <q-tab name="consolidado" label="Consolidado" icon="savings" />
   </q-tabs>

   <q-tab-panels v-model="tab" animated class="">
    <q-tab-panel name="entradas" class="">
     <q-card-section>
      <div class="row items-center no-wrap">
       <div class="col">
        <div class="text-h6">Entrada</div>
        <div class="text-subtitle2 text-positive">Positiva financeira</div>
       </div>

       <div class="col-auto">
        <q-btn color="grey-7" round flat icon="more_vert">
         <q-menu cover auto-close>
          <q-list>
           <q-item clickable @click="mostraModalEntrada = true">
            <q-item-section>Cadastrar Entrada</q-item-section>
           </q-item>
          </q-list>
         </q-menu>
        </q-btn>
       </div>
      </div>
     </q-card-section>

     <q-card-section>
      <q-list bordered separator>
       <q-item clickable v-ripple v-for="e in entradas" v-bind:key="e" @click="abreEditaEntrada(e)">
        <q-item-section>
         <q-item-label>R$ {{ e.dados.valor }}</q-item-label>
         <q-item-label caption>{{ e.dados.descricao }}</q-item-label>
        </q-item-section>
        <q-badge v-if="e.dados.fechada" color="green" text-color="black" label="F" />
        <q-badge v-else color="orange" text-color="black" label="A" />
       </q-item>
      </q-list>
     </q-card-section>

     <q-separator />

     <q-card-actions>
     </q-card-actions>
    </q-tab-panel>

    <q-tab-panel name="saidas" class="">
     <q-card-section>
      <div class="row items-center no-wrap">
       <div class="col">
        <div class="text-h6">Saída</div>
        <div class="text-subtitle2 text-negative">Negativa financeira</div>
       </div>
      </div>
     </q-card-section>

     <q-card-section>
      <q-list bordered separator>
       <q-item clickable v-ripple v-for="s in saidas" v-bind:key="s" @click="abreEditaSaida(s)">
        <q-item-section>
         <q-item-label>R$ {{ s.dados.valor }}</q-item-label>
         <q-item-label caption>{{ s.dados.descricao }}</q-item-label>
        </q-item-section>
        <q-badge v-if="s.dados.fechada" color="green" text-color="black" label="F" />
        <q-badge v-else color="orange" text-color="black" label="A" />
       </q-item>
      </q-list>
     </q-card-section>

     <q-separator />

     <q-card-actions>
     </q-card-actions>
    </q-tab-panel>

    <q-tab-panel name="consolidado" class="">
     <q-card-section>
      <div class="row items-center no-wrap">
       <div class="col">
        <div class="text-h6">Consolidado</div>
        <div class="text-subtitle2 text-primary">Consolidação financeira</div>
       </div>
      </div>
     </q-card-section>

     <q-card-section>
      <q-list bordered separator>
       <q-item v-ripple class="bg-green-1">
        <q-item-section>
         <q-item-label>R$ {{ valorTotalEntradas }}</q-item-label>
         <q-item-label caption>Total de Entradas</q-item-label>
        </q-item-section>
       </q-item>
       <q-item v-ripple class="bg-red-1">
        <q-item-section>
         <q-item-label>R$ {{ valorTotalSaidas }}</q-item-label>
         <q-item-label caption>Total de Saídas</q-item-label>
        </q-item-section>
       </q-item>
       <q-item v-ripple class="bg-blue-1">
        <q-item-section>
         <q-item-label v-if="valorTotalSaidas > valorTotalEntradas" class="text-red">R$ {{ valorTotalEntradas -
          valorTotalSaidas }}</q-item-label>
         <q-item-label v-else>R$ {{ valorTotalEntradas - valorTotalSaidas }}</q-item-label>
         <q-item-label caption>Posição consolidada</q-item-label>
        </q-item-section>
       </q-item>
      </q-list>
     </q-card-section>

     <q-separator />

     <q-card-actions>
      <q-btn color="primary" @click="abreInsereEntrada">Inserir Entrada</q-btn>
      <q-btn color="red" @click="abreInsereSaida">Inserir Saída</q-btn>
     </q-card-actions>
    </q-tab-panel>

   </q-tab-panels>
  </q-card>

  <q-dialog v-model="mostraModalEntrada" persistent>
   <q-card style="min-width: 70vw">
    <q-card-section>
     <div class="text-h6">Seu Recebimento</div>
     <div class="text-subtitle2 text-green">Entrada financeira</div>
    </q-card-section>

    <q-separator />

    <q-card-section class="q-pt-none">
     <div class="row items-center no-wrap">
      <div class="col">
       <q-input filled v-model="entrada.descricao" label="Descrição" hint="Detalhe do recebimento" lazy-rules
        :rules="[(val) => (val && val.length > 0) || 'Digite uma descrição']" />
      </div>
     </div>
     <div class="row items-center">
      <div class="col-sm-4 col-xs-12">
       <q-input filled v-model="entrada.valor" type="number" label="Valor" hint="Valor do recebimento" lazy-rules
        :rules="[(val) => (val && val.length > 0) || 'Digite um valor']" />
      </div>
      <div class="col-sm-4 col-xs-12">
       <q-input filled v-model="entrada.dtregistro" type="date" label="Data" hint="Data do recebimento" lazy-rules
        :rules="[(val) => (val && val.length > 0) || 'Digite uma data']" />
      </div>
      <div class="col-sm-4 col-xs-12">
       <q-checkbox left-label v-model="entrada.fechada" label="Fechado" />
      </div>
     </div>
    </q-card-section>

    <q-separator />

    <q-card-actions align="right" class="text-primary">
     <q-btn flat label="Cancela" color="orange" v-close-popup />
     <q-btn v-if="entrada.id" flat label="Exclui" color="red" @click="excluiEntrada" />
     <q-btn flat :label="!entrada.id ? 'Insere' : 'Atualiza'" color="positive" v-close-popup @click="cadastraEntrada" />
    </q-card-actions>
   </q-card>
  </q-dialog>

  <q-dialog v-model="mostraModalSaida" persistent>
   <q-card style="min-width: 70vw">
    <q-card-section>
     <div class="text-h6">Seu Pagamento</div>
     <div class="text-subtitle2 text-red">Saída financeira</div>
    </q-card-section>

    <q-separator />

    <q-card-section class="q-pt-none">
     <div class="row items-center no-wrap">
      <div class="col">
       <q-input filled v-model="saida.descricao" label="Descrição" hint="Detalhe do pagamento" lazy-rules
        :rules="[(val) => (val && val.length > 0) || 'Digite uma descrição']" />
      </div>
     </div>
     <div class="row items-center">
      <div class="col-sm-4 col-xs-12">
       <q-input filled v-model="saida.valor" type="number" label="Valor" hint="Valor do pagamento" lazy-rules
        :rules="[(val) => (val && val.length > 0) || 'Digite um valor']" />
      </div>
      <div class="col-sm-4 col-xs-12">
       <q-input filled v-model="saida.dtregistro" type="date" label="Data" hint="Data do pagamento" lazy-rules
        :rules="[(val) => (val && val.length > 0) || 'Digite uma data']" />
      </div>
      <div class="col-sm-4 col-xs-12">
       <q-checkbox left-label v-model="saida.fechada" label="Fechado" />
      </div>
     </div>
    </q-card-section>

    <q-separator />

    <q-card-actions align="right" class="text-primary">
     <q-btn flat label="Cancela" color="orange" v-close-popup />
     <q-btn v-if="saida.id" flat label="Exclui" color="red" @click="excluiSaida" />
     <q-btn flat :label="!saida.id ? 'Insere' : 'Atualiza'" color="positive" v-close-popup @click="cadastraSaida" />
    </q-card-actions>
   </q-card>
  </q-dialog>
 </q-page>
</template>

<script>
/* eslint-disable no-unused-vars */
/* eslint-disable no-undef */

import DateOpsMixin from '@/mixins/DateOpsMixin';
import EntradasService from '@/services/EntradasService';
import SaidasService from '@/services/SaidasService';

export default {
 mixins: [
  DateOpsMixin
 ],
 data() {
  return {
   tab: "consolidado",
   mesAtual: new Date(),
   entrada: {
    ano: null,
    descricao: null,
    dtregistro: null,
    mes: null,
    fechada: true,
    valor: 0
   },
   saida: {
    ano: null,
    descricao: null,
    dtregistro: null,
    mes: null,
    fechada: true,
    valor: 0
   },
   entradas: [],
   saidas: [],
   valorTotalEntradas: 0,
   valorTotalSaidas: 0,
   mostraModalEntrada: false,
   mostraModalSaida: false
  };
 },
 async mounted() {
  let d = new Date();
  //this.get(d.getMonth() + 1, d.getFullYear());
  await this.getEntradasMesAno(d.getMonth(), d.getFullYear());
  await this.getSaidasMesAno(d.getMonth(), d.getFullYear());
 },
 methods: {
  formataMoeda(n) {
   n.toLocaleString("pt-BR", { style: "currency", currency: "BRL" });
  },
  limpaEntrada() {
   this.entrada = {
    ano: null,
    descricao: null,
    dtregistro: null,
    mes: null,
    fechada: true,
    valor: 0
   }
  },
  abreInsereEntrada() {
   this.limpaEntrada();
   this.mostraModalEntrada = true;
  },
  abreEditaEntrada(e) {
   this.entrada = e.dados;
   this.entrada.id = e.id;
   this.entrada.dtregistro = this.nano2IsoDate(this.entrada.dtregistro);
   console.log(this.entrada);
   this.mostraModalEntrada = true;
  },
  excluiEntrada() {
   if (this.entrada.id) {
    this.$q.dialog({
     title: 'Confirmação',
     message: 'Confirma a exclusão da Entrada',
     cancel: true,
     persistent: true
    }).onOk(() => {
     EntradasService.removeEntradasC(this.entrada.id).then(() => {
      this.mostraModalEntrada = false;
      this.$q.notify({
       progress: true,
       message: 'Entrada excluída',
       color: 'primary',
       /*avatar: 'https://cdn.quasar.dev/img/boy-avatar.png',*/
       actions: [
        { label: 'OK', color: 'yellow', handler: () => { } }
       ]
      })
      this.getEntradasMesAno(this.mesAtual.getMonth(), this.mesAtual.getFullYear());
     });
    })
   }
  },
  cadastraEntrada() {
   let dtsplit = this.entrada.dtregistro.split('-');
   let dt = new Date(Number.parseInt(dtsplit[0]), Number.parseInt(dtsplit[1]) - 1, Number.parseInt(dtsplit[2]));
   this.entrada.dtregistro = dt;
   this.entrada.ano = dt.getFullYear();
   this.entrada.mes = dt.getMonth();
   this.$q.loading.show();
   if (this.entrada.id) {
    EntradasService.updEntradasC(this.entrada).then(r => {
     this.getEntradasMesAno(this.mesAtual.getMonth(), this.mesAtual.getFullYear());
    })
   } else {
    EntradasService.addEntradasC(this.entrada).then(r => {
     this.getEntradasMesAno(this.mesAtual.getMonth(), this.mesAtual.getFullYear());
    })
   }
  },
  async getEntradasMesAno(mes, ano) {
   this.$q.loading.show()
   let dados = await EntradasService.getRecebimentosMesAnoAsync(mes, ano);
   this.entradas = dados.entradas;
   this.valorTotalEntradas = dados.valorTotalEntradas;
   this.limpaEntrada();
   this.$q.loading.hide();
  },
  limpaSaida() {
   this.saida = {
    ano: null,
    descricao: null,
    dtregistro: null,
    mes: null,
    fechada: true,
    valor: 0
   }
  },
  abreInsereSaida() {
   this.limpaSaida();
   this.mostraModalSaida = true;
  },
  abreEditaSaida(e) {
   this.saida = e.dados;
   this.saida.id = e.id;
   this.saida.dtregistro = this.nano2IsoDate(this.saida.dtregistro);
   console.log(this.saida);
   this.mostraModalSaida = true;
  },
  excluiSaida() {
   if (this.saida.id) {
    this.$q.dialog({
     title: 'Confirmação',
     message: 'Confirma a exclusão da Saida',
     cancel: true,
     persistent: true
    }).onOk(() => {
     SaidasService.removeSaidasC(this.saida.id).then(() => {
      this.mostraModalSaida = false;
      this.$q.notify({
       progress: true,
       message: 'Saida excluída',
       color: 'primary',
       /*avatar: 'https://cdn.quasar.dev/img/boy-avatar.png',*/
       actions: [
        { label: 'OK', color: 'yellow', handler: () => { } }
       ]
      })
      this.getSaidasMesAno(this.mesAtual.getMonth(), this.mesAtual.getFullYear());
     });
    })
   }
  },
  cadastraSaida() {
   let dtsplit = this.saida.dtregistro.split('-');
   let dt = new Date(Number.parseInt(dtsplit[0]), Number.parseInt(dtsplit[1]) - 1, Number.parseInt(dtsplit[2]));
   this.saida.dtregistro = dt;
   this.saida.ano = dt.getFullYear();
   this.saida.mes = dt.getMonth();
   this.$q.loading.show();
   if (this.saida.id) {
    SaidasService.updSaidasC(this.saida).then(r => {
     this.getSaidasMesAno(this.mesAtual.getMonth(), this.mesAtual.getFullYear());
    })
   } else {
    SaidasService.addSaidasC(this.saida).then(r => {
     this.getSaidasMesAno(this.mesAtual.getMonth(), this.mesAtual.getFullYear());
    })
   }
  },
  async getSaidasMesAno(mes, ano) {
   this.$q.loading.show()
   let dados = await SaidasService.getRecebimentosMesAnoAsync(mes, ano);
   this.saidas = dados.saidas;
   this.valorTotalSaidas = dados.valorTotalSaidas;
   this.limpaSaida();
   this.$q.loading.hide();
  }
 }
}
</script>

