<template>
  <q-page class="q-pa-lg window-height window-width row justify-center ">
    <div class="col-1 col-md-3">

    </div>
    <div class="col-10 col-md-6">
      <q-list bordered separator>

        <q-item clickable v-ripple tag="a" to="/registros/mes-atual">
          <q-item-section avatar>
            <q-icon color="primary" name="today" />
          </q-item-section>
          <q-item-section>
            <q-item-label>Mês atual</q-item-label>
            <q-item-label caption>{{ dtAtual.getFullYear() }} - {{ dtAtual.getMonth() + 1 }}</q-item-label>
          </q-item-section>
        </q-item>
        <q-item clickable v-ripple @click="sair">
          <q-item-section avatar>
            <q-icon color="red" name="logout" />
          </q-item-section>
          <q-item-section>Sair</q-item-section>
        </q-item>
      </q-list>
    </div>

    <div class="col-1 col-md-3">

    </div>
  </q-page>
</template>

<script>
/* eslint-disable no-unused-vars */
/* eslint-disable no-undef */

import LoginService from '@/services/LoginService';

export default {
  name: 'HomeView',
  components: {

  },
  data() {
    return {
      dtAtual: new Date()
    }
  },
  mounted() {
    this.dtAtual = new Date();
    let mail = LoginService.getUser().email;
    let pArroba = mail.indexOf('@');
    if (pArroba >= 0)
      this.$root.$refs.header.userLogged = LoginService.getUser().email.substring(0, pArroba);
  },
  methods: {
    sair() {
      this.$q.loading.show();
      LoginService.exit()
        .then(() => {
          console.log("saiu");
          this.$q.loading.hide();
          this.$root.$refs.header.logado = LoginService.isUserLoggedIn();
          this.$router.push("/login");
        })
        .catch((error) => {
          console.log(error);
          this.$q.loading.hide();
        });
    }
  }
}
</script>
